import { BaseEntity } from "../../masters/base-entity";
import { Department } from "../../department/masters/department";
import { LookupEntity } from "../../lookup-module/masters/lookup-entity";
import { Block } from "./block";
import { Branch } from "./branch";
import { Floor } from "./floor";

export class ServiceCenter extends BaseEntity{
	serviceCenterId : number;
	serviceCenterName: string;
	serviceCenterCode: string;
	serviceCenterType: LookupEntity = new LookupEntity();
	description: string;
	department: Department = new Department();
	floor: Floor = new Floor();
	block : Block = new Block();
	startedOn : Date;
	site: Branch = new Branch();
	lastInactivatedOn: Date;
	active: Boolean;
	attachStore: Boolean;

	constructor(){
		super();
	}
} 
